@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.meanings {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: thin;
  height: 55vh;
  border-radius: 10px;
  border: 10px groove rgb(185, 185, 185);
  padding: 10px 20px;
  
}

@media (max-width: 700px) {
  .meanings {
    margin-left: 20px;
    height: 50vh;
    width: 80%;
  }
}

.subTitle {
  font-size: 5vw;
  font-family: "Montserrat", sans-serif;
}

.singleMean {
  background: "transparent";
  font-family: "Montserrat", sans-serif;
  box-shadow:  0.5px 0.5px 5px 1px rgba(29, 15, 15, 0.589);
  border-radius: 10px;
  padding: 10px 20px ;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  margin: 10px 0px;
  letter-spacing: 1px;
  
    font-weight: 900;
  }


.audioBar {
  width: 100%;
}

@media (max-width: 700px) {
  .singleMean {
    background: transparent;
    font-size: 13px;
    grid-gap: 3px;
    gap: 3px;
    margin: 3px 0;
    padding: 0 0 10px 0;
    box-shadow:  0.5px 0.5px 5px 1px rgba(15, 15, 15, 0.274);
    padding: 10px 20px;
    
  }
 
}

.title {
  font-size: 7vw;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
}

.header {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  height: 35vh;
  width: 100%;
  
}

@media (max-width: 900px) {
  .title {
    font-size: 11vw;
  }
}

.inputs {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.search,
.select {
  width: 40%;
}

